/**
 * 
 * @param {*} str
 * @returns
 */
export function escape2Html(str) {
	if (!str) return str
	var arrEntities = {
		'lt': '<',
		'gt': '>',
		'nbsp': ' ',
		'amp': '&',
		'quot': '"'
	}
	return str.replace(/&(lt|gt|nbsp|amp|quot);/ig, function(all, t) {
		return arrEntities[t]
	})
}

/**
 * 
 * @param {*} sHtml
 * @returns
 */
export function html2Escape(sHtml) {
	if (!sHtml) return sHtml
	return sHtml.replace(/[<>&"]/g, function(c) {
		return {
			'<': '&lt;',
			'>': '&gt;',
			'&': '&amp;',
			'"': '&quot;'
		} [c]
	})
}

