/**
 * getTabBaruni-app
 * custom-tab-bar
 * 
 * 1.custom-tab-bar
 * 2.pages.jsontabBar
 */
export function getTabBar() {
	return {
		setData(obj) {
			if (typeof this.$mp?.page?.getTabBar === 'function' &&
				this.$mp?.page?.getTabBar()) {
				this.$mp.page.getTabBar().setData(obj)
			} else {
				console.log("getTabBar()")
			}
		}
	}
}
