/**
 * dataset
 * e.currentTarget.dataset
 * data-
 *
 * @param {*} event
 * @param {*} dataSet
 */
export function handleDataset(event, dataSet = {}) {
	if (event && !event.currentTarget) {
		if (dataSet.tagId) {
			event.currentTarget = {
				id: dataSet.tagId
			}
		} else {
			event.currentTarget = {
				dataset: dataSet
			}
		}
	}
}
